/* Основной контейнер */
.main-menu {
  background-color: #352d2d; /* Тёмный фон для контраста */
  color: #ffffff; /* Белый текст */
  padding: 20px;
  min-height: 100vh; /* Высота экрана */
  font-family: 'Arial', sans-serif;
}

/* Заголовок */
.main-menu-title {
  font-size: 2.5rem;
  color: #ff6f00; /* Ярко-оранжевый акцент */
  margin-bottom: 20px;
  text-align: center;
}

/* Контейнер карточек */
.menu-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); /* Адаптивная сетка */
  gap: 20px;
  justify-content: center;
}

/* Карточки */
.menu-card {
  background: linear-gradient(145deg, #d9d9d9ed, #ffffff); /* Светло-серый фон с градиентом */
  border-radius: 15px; /* Закругленные углы */
  text-align: center;
  padding: 20px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2), 
              inset 0 -4px 8px rgba(0, 0, 0, 0.1), /* Внутренняя тень */
              inset 0 4px 8px rgba(255, 255, 255, 0.6); /* Лёгкий внутренний 3D-эффект */
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.menu-card:hover {
  transform: translateY(-5px); /* Лёгкое поднятие при наведении */
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3); /* Усиление тени при наведении */
}

/* Изображения */
.card-image {
  width: 100%; /* Оптимальный размер изображения */
  border-radius: 10px;
  margin-bottom: 15px;
}

/* Кнопки */
.card-button {
  margin-top: 10px;
  padding: 12px 20px;
  background-color: rgba(77, 74, 71, 0.9); /* Ярко-оранжевый полупрозрачный фон */
  color: #ffffff; /* Белый текст */
  font-size: 1rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.card-button:hover {
  background-color: rgb(91, 78, 68); /* Полностью насыщенный оранжевый */
  transform: scale(1.05); /* Лёгкое увеличение при наведении */
}

/* Адаптивность */
@media (max-width: 600px) {
  .menu-container {
    grid-template-columns: repeat(2, 1fr); /* Две колонки на маленьких экранах */
    gap: 15px;
  }

  .card-button {
    padding: 10px 15px; /* Уменьшенные кнопки */
  }
}
