/* Основной контейнер страницы */
.video-page {
    background-color: #2a2a2a; /* Темно-серый брутальный фон */
    color: #ffffff; /* Белый текст для контраста */
    padding: 20px;
    text-align: center;
    min-height: 100vh; /* Высота экрана */
    font-family: 'Arial', sans-serif;
  }
  
  /* Заголовок */
  .video-title {
    font-size: 2.5rem;
    color: #ff6f00; /* Ярко-оранжевый для заголовка */
    margin-bottom: 20px;
  }
  
  /* Видео плеер */
  .video-player {
    width: 80%;
    max-width: 800px;
    border: 3px solid #444444; /* Темная рамка вокруг видео */
    border-radius: 10px;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.8);
    margin: 20px auto;
  }
  
  /* Описание */
  .video-description {
    font-size: 1.2rem;
    margin: 20px auto;
    max-width: 70%;
    color: #d4d4d4; /* Светло-серый для текста */
  }
  
  /* Контейнер для кнопки */
  .button-container {
    margin-top: 30px;
  }
  
  /* Кнопка */
  .back-button {
    padding: 10px 20px;
    background-color: #444; /* Темно-серый */
    color: #ff6f00; /* Ярко-оранжевый текст */
    border: 2px solid #ff6f00; /* Оранжевая рамка */
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.6);
  }
  
  .back-button:hover {
    background-color: #ff6f00; /* Оранжевый фон при наведении */
    color: #ffffff; /* Белый текст при наведении */
    transform: scale(1.1); /* Легкое увеличение */
  }
  